import { chrome } from "../utils/polyfill.ts";
import {
    MSG_INFO_BLOCKED_CONTENT,
    MSG_INFO_MONTHLY_NOTIFICATION,
    MSG_INFO_RELEASE_NOTIFICATION,
    SETTING_MALICIOUS_NOTIFICATION,
    SETTING_MONTHLY_NOTIFICATION,
    SETTING_NEW_RELEASE_NOTIFICATION,
    MSG_BREACH_NOTIFICATION,
} from "../app/scripts/app-consts";
import {
    blockContentAndContinue,
    displayMonthlyNotification,
    displayReleaseNotification,
    displayBreachNotification,
} from "./content-notifications.js";
import { settingsGetAsync } from "../app/scripts/ui-utils/ui-utils";

const getQueryableSrc = (fullUrl = "") => {
    try {
        const u = new URL(fullUrl);
        return fullUrl.replace(`${u.protocol}//${u.host}`, "");
    } catch (error) {
        return fullUrl;
    }
};

chrome.runtime.onMessage.addListener(async (message) => {
    switch (message.type) {
        case "blockMedia":
            handleBlockMedia(message);
            return true;
        case "blockXPath":
            handleBlockXPath(message);
            return true;
        case MSG_INFO_BLOCKED_CONTENT:
            handleBlockedContent(message);
            return true;
        case MSG_INFO_MONTHLY_NOTIFICATION:
            handleMonthlyNotification(message);
            return true;
        case MSG_INFO_RELEASE_NOTIFICATION:
            handleReleaseNotification(message);
            return true;
        case MSG_BREACH_NOTIFICATION:
            displayBreachNotification(message.breachResults);
            return true;
        default:
            return true;
    }
});

function handleBlockMedia(message) {
    const src = getQueryableSrc(message.src);
    let element;
    if (message.mediaType == "image") {
        element = document.querySelector(`img[src$="${src}"]`);
    } else if (message.mediaType == "video") {
        element = document.querySelector(`video[src$="${src}"]`);
    }
    if (element) {
        // remove the element from the DOM
        element.style.display = "none";
    }
}

// See https://developer.mozilla.org/en-US/docs/Web/XPath/Introduction_to_using_XPath_in_JavaScript
function handleBlockXPath(message) {
    console.debug('INS: Blocking xpath: ', { message });
    const xpath = message.xpath;

    if (!xpath) {
        console.debug('INS: No xpath to block');
        return;
    }

    const xpathResult = document.evaluate(
        xpath,
        document,
        null,
        XPathResult.ANY_TYPE, // https://developer.mozilla.org/en-US/docs/Web/XPath/Introduction_to_using_XPath_in_JavaScript#xpathresult_defined_constants
        null
    );
    if (!xpathResult) {
        console.debug('INS: No xpath result found for xpath: ', xpath);
        return;
    }

    let node = xpathResult.iterateNext();
    if (!node) {
        console.debug('INS: No node found for xpath: ', xpath);
        return;
    }

    // if the node is a link
    const parent = getParentWithImageChildOrContent(node);
    if (parent) {
        console.debug('INS: Removing parent of xpath result: ', { parent });
        parent.remove();
    }
}

// Finds the first parent of the node that is an image, or has an image as a child. Returns the node itself if it is an image.
// Stops searching after height levels of parent nodes
// Returns null if no parent is found
function getParentWithImageChildOrContent(node, height = 5) {
    if (height <= 0) {
        return null;
    }

    // image or link with an image src
    if (isImageElement(node)) {
        return node.parentElement;
    }

    if (node.children.length > 0) {
        for (const child of node.children) {
            const result = getParentWithImageChildOrContent(child, height);
            if (result) {
                return result;
            }
        }
    }

    if (node.parentElement) {
        return getParentWithImageChildOrContent(node.parentElement, height - 1);
    }

    return null;
}

function isImageElement(node) {
    if (
        node.tagName === "IMG" || node.tagName === "VIDEO" ||
        (node.tagName === "A" && (node.href && includesImageExtension(node.href))) ||
        node.tagName === "PICTURE"
    ) {
        return true;
    }
    return false;
}

function includesImageExtension(url) {
    return /\.(png|jpg|jpeg|gif|bmp|svg|webp)([?#].*)?$/i.test(url);
}

async function handleBlockedContent(message) {
    // eslint-disable-next-line max-len
    const isMaliciousNotificationEnabled = await settingsGetAsync(SETTING_MALICIOUS_NOTIFICATION);
    if (isMaliciousNotificationEnabled) {
        const src = getQueryableSrc(message.src);
        const malware = message.malware;
        const tabId = message.tabId;
        blockContentAndContinue(src, malware, tabId);
        return;
    }
    console.debug(`INS: Malicious notifications are disabled`);
}

async function handleMonthlyNotification(_message) {
    const isMonthlyNotificationEnabled = await settingsGetAsync(SETTING_MONTHLY_NOTIFICATION);
    if (isMonthlyNotificationEnabled) {
        displayMonthlyNotification();
        return;
    }
    console.debug(`INS: Monthly notifications are disabled`);
}

async function handleReleaseNotification(message) {
    const isNewReleaseNotificationEnabled = await settingsGetAsync(SETTING_NEW_RELEASE_NOTIFICATION);
    if (isNewReleaseNotificationEnabled) {
        displayReleaseNotification(message.releaseNotes);
        chrome.storage.local.set({ "showReleaseNotificationFlag": false });
        return;
    }
    console.debug(`INS: New Release notifications are disabled`);
}
