/*
* 
*  Refrence: https://developer.mozilla.org/en-US/docs/Web/API/Attribution_Reporting_API
* 
*/

import {
    MSG_TAB_ID_GET,
} from '@/app/scripts/app-consts';
import {
    isAdsProtectionActive,
    isAttrReportingExcluded,
    recordAttributionblock
} from '@/content-scripts/common-utils';

const pageUrl = window.location.hostname;

// Handle <img> elements
export function handleImageElements(elements: NodeListOf<HTMLImageElement>, tabId:number): void {
    elements.forEach((element) => {
        if (element.hasAttribute('attributionsrc')) {
            element.removeAttribute('attributionsrc');
            console.log(`ARW: Removed attributionsrc from <img> element src: ${element.src}`);
            recordAttributionblock(tabId, pageUrl);
        }
    });
}

// Handle <script> elements
export function handleScriptElements(elements: NodeListOf<HTMLScriptElement>, tabId:number): void {
    elements.forEach((element) => {
        if (element.hasAttribute('attributionsrc')) {
            element.remove();
            // log script content
            console.log(`ARW: Removed <script> element with attributionsrc: query: ${element.innerHTML}`);
            recordAttributionblock(tabId, pageUrl);
        }
    });
}

// Handle <a> elements
export function handleAnchorElements(elements: NodeListOf<HTMLAnchorElement>, tabId:number): void {
    elements.forEach((element) => {
        if (element.hasAttribute('attributionsrc')) {
            element.removeAttribute('attributionsrc');
            console.log(`ARW: Removed attributionsrc from <a> element: ${element.href}`);
            recordAttributionblock(tabId, pageUrl);
        }
    });
}

export function getCurrentTabId(): Promise<{ tabId: number }> {
    return new Promise((resolve, reject) => {
        chrome.runtime.sendMessage({ type: MSG_TAB_ID_GET }, (resp) => {
            if (!resp) {
                reject(new Error("Error getting tab info"));
            } else {
                resolve(resp);
            }
        });
    });
}

// Loop through all elements and apply actions
export function processElements(tabId:number): void {

    const allImages: NodeListOf<HTMLImageElement> = document.querySelectorAll('img[attributionsrc]');
    const allScripts: NodeListOf<HTMLScriptElement> = document.querySelectorAll('script');
    const allAnchors: NodeListOf<HTMLAnchorElement> = document.querySelectorAll('a');

    handleImageElements(allImages, tabId);
    handleScriptElements(allScripts, tabId);
    handleAnchorElements(allAnchors, tabId);

}


document.addEventListener('DOMContentLoaded', async () => {
    try {

        const { tabId } = await getCurrentTabId();

        const ShouldBlockAds: boolean = await isAdsProtectionActive(tabId, pageUrl);
        const isAttrRepExcluded: boolean = await isAttrReportingExcluded(pageUrl);

        if (ShouldBlockAds && !isAttrRepExcluded) {
            processElements(tabId);
        }

    } catch (err) {
        console.error('ARW: Error getting tab info:', err);
    }
});
