import { injectJavascript, createBlockUrl, browserName, urlHost, injectJsFile, isMV3 } from '../utils/utils';
import { chrome } from '../utils/polyfill.ts';
import { tldRiskLevel } from '../utils/downloads/downloadConsts';
import { checkoutRegex, latestTssBodyPatterns, latestTssTitlePatterns } from '../utils/patterns';
import { MSG_SETTINGS_GET, MSG_TAB_ID_GET, SETTING_SKIMMER_PROTECTION } from '../app/scripts/app-consts';
import { SetTss } from '../app/scripts/tss-setup';

const trojanPattern = /trojan *spyware *alert *- *error *code: *#.*/i;
const cardAutocompleteAttrs = ["cc-name", "cc-number", "cc-csc", "cc-exp-month", "cc-exp-year", "cc-exp", "cc-type"];

var mbtss = {};
var detected = false;
var excluded = false;
var tabId;
mbtss.nonce =
    Math.random().toString(36).substring(2, 15) +
    Math.random().toString(36).substring(2, 15) +
    Math.random().toString(36).substring(2, 15) +
    Math.random().toString(36).substring(2, 15);
mbtss.warningRegexes = [
    /(\/warning.mp3)$/i,
    /(\/0wa0rni0ng0.mp3)$/i
];

let mbtssUrl = window.location.href;
let lastFullScreenData = {};

const isSafari = browserName() === 'Safari';

const isSusAudioChild = (children) =>
    children &&
    children[0] &&
    children[0].tagName === "SOURCE" &&
    children[0].type && children[0].type.toLowerCase() === "audio/mpeg" &&
    mbtss.warningRegexes.some((e) => e.test(children[0].src));

const isSusAudioPlayer = () => {
    // BG-883
    const audio = document.getElementById('beep');
    if (
        audio &&
        audio.tagName === 'AUDIO' &&
        audio.hasAttribute('autoplay') &&
        isSusAudioChild(audio.children)
    ) {
        console.debug("TSS: Suspicious audio auto-player detected");
        return true;
    }
    return false;
};

const isTrojanScam = () => {
    const h2Tags = document.getElementsByTagName('h2');
    if (!h2Tags) {
        return false;
    }

    for (let i = 0; i < h2Tags.length; i++) {
        if (h2Tags[i] && trojanPattern.test(h2Tags[i].innerText)) {
            return true;
        }
    }
    return false;
};

const isCheckoutSkimmer = () => {
    if (window.location.protocol === "http:") {
        const inputs = document.getElementsByTagName("input");

        return Array.from(inputs).some(
            (input) =>
                input.attributes &&
                input.attributes.autocomplete &&
                input.attributes.autocomplete.value &&
                cardAutocompleteAttrs.includes(
                    input.attributes.autocomplete.value.toLowerCase()
                )
        );
    }
};

mbtss.isSuspiciousPage = () => isSusAudioPlayer() || isTrojanScam();

mbtss.onSuspiciousPage = () => {
    console.debug("TSS: Caught a suspicious page");
    mbtss.onDetection("scam", "suspiciousPage");
};

mbtss.onDetection = (type, subtype) => {
    if (!detected && !excluded) {
        chrome.runtime.sendMessage(
            { type: "detection", parameters: { type, subtype, url: mbtssUrl } },
            (response) => {
                console.debug(`TSS: Received onDetection message response ${JSON.stringify(response.detect)}`);
                response.detect ?
                    mbtss.onDetectionImpl(type, subtype) :
                    mbtss.onExclusion("detection");
            }
        );
    }
};

mbtss.getTabId = () => {
    return new Promise((resolve, reject) => {
        chrome.runtime.sendMessage({ type: MSG_TAB_ID_GET },
            (resp) => {
                if (!resp || !resp.tabId) {
                    reject(new Error("Error getting tab info"));
                } else {
                    resolve(resp.tabId);
                }
            });
    });
};

mbtss.isExcluded = (url) => {
    return new Promise((resolve) => {
        chrome.runtime.sendMessage(
            { type: "isExcluded", parameters: { type: "scam", url: url, tabId: tabId } },
            (response) => {
                resolve(response.excluded);
            }
        );
    });
};

mbtss.onDetectionImpl = async (type, subtype) => {
    if (isSafari) {
        const isExcluded = await mbtss.isExcluded(urlHost(mbtssUrl));
        if (isExcluded) {
            return;
        }
    }

    console.debug("TSS: Removing suspicious iframes");
    detected = true; // suppress "are you sure you want to leave this page?" popups
    // delete iframes so they can't show the popups either
    document.querySelectorAll("iframe").forEach((element) => {
        element.remove();
    });

    const prevUrl = isSafari ? document.referrer : null;
    const blockUrl = createBlockUrl(null, mbtssUrl, type, subtype, null, null, prevUrl);
    window.location.replace(blockUrl);
};

mbtss.onExclusion = (trig) => {
    excluded = true;
    if (trig != 'send-mesage') {
        window.postMessage({ type: "exclude", nonce: mbtss.nonce }, "*");
    }
};

mbtss.isRepeated = (data, interval, threshold) => {
    // return true if >= N occurrences in interval (milliseconds)
    let thisTime = Date.now();
    if (data.lastTime) {
        let thisInterval = thisTime - data.lastTime;
        if (thisInterval < interval) {
            data.lastCount++;
            if (data.lastCount >= threshold) {
                return true;
            }
        } else {
            data.lastTime = thisTime;
            data.lastCount = 1;
        }
    } else if (threshold === 1) {
        data.lastTime = thisTime;
        data.lastCount = 1;
        return true;
    } else {
        data.lastTime = thisTime;
        data.lastCount = 1;
    }
    return false;
};

mbtss.onFullScreenChange = () => {
    console.debug("TSS: Caught suspicious full screen spamming");
    // detect if >= 10 repeated full screen changes within 10 seconds
    console.debug(
        `TSS: Checking if repeated ${10} times for interval ${10000} against data: `,
        lastFullScreenData
    );
    if (mbtss.isRepeated(lastFullScreenData, 10000, 10)) {
        mbtss.onDetection("scam", "fullScreenLoop");
    }
};

mbtss.onAuthRequired = () => {
    console.debug("TSS: Caught suspicious auth required - investigating for tech support scam");
    // detect if page looks like a tech support scam
    if (mbtss.isSuspiciousPage()) {
        mbtss.onDetection("scam", "authRequiredLoop");
    }
};

mbtss.initScriptListener = () => {
    window.addEventListener("message", (event) => {
        if (
            event.source === window &&
            event.data.nonce === mbtss.nonce &&
            event.data.type === "scam"
        ) {
            mbtss.onDetection(event.data.type, event.data.subtype);
        }
    }, false);
};

// communicate with the hosting page - https://developer.chrome.com/extensions/content_scripts#host-page-communication
mbtss.setup = () => {
    mbtss.initScriptListener();
    window.addEventListener("beforeunload", (event) => {
        //TODO: We need to detect and prevent: Navigated to https://www.msn.com/g00/en-us?i10c.ua=1&i10c.encReferrer=&i10c.dv=15
        event.stopImmediatePropagation();
    }, false);

    document.addEventListener("fullscreenchange", mbtss.onFullScreenChange, false);
    document.addEventListener("webkitfullscreenchange", mbtss.onFullScreenChange, false);
    document.addEventListener("mozfullscreenchange", mbtss.onFullScreenChange, false);
    chrome.runtime.onMessage.addListener((message, _ignored, _ignore) => {
        if (message.type === "authRequired") {
            mbtss.onAuthRequired();
        }
    });

    //NOTE: JSON.stringify(mbtss) doesn't work, doesnt include methods, only the nonce
    if (!isMV3()) {
        injectJavascript(`
            (function() {
                const TssFunc = ${SetTss.toString()};
                TssFunc("${mbtss.nonce}", ${mbtss.isRepeated.toString()}, "content-tss")
            })();
        `);
    }

    if (isCheckoutSkimmer()) {
        console.debug("TSS: Caught unsafe checkout");
        mbtss.onDetection("scam", "suspiciousPage");
    }

    const detectSuspiciousPage = () => {
        if (latestTssTitlePatterns.test(document.head.outerHTML)) {
            console.debug('TSS: Page blocked due to malicious patterns in the head content.');
            return mbtss.onSuspiciousPage();
        }
        const hasSuspiciousPattern = latestTssBodyPatterns.some((element) => document.body.outerHTML.includes(element));
        if (hasSuspiciousPattern) {
            return mbtss.onSuspiciousPage();
        }
    };

    if (document.readyState !== 'loading') {
        detectSuspiciousPage();
    } else {
        document.addEventListener('DOMContentLoaded', function () {
            detectSuspiciousPage();
        });
    }

    setTimeout(() => {
        if (mbtss.isSuspiciousPage()) {
            mbtss.onSuspiciousPage();
        }
    }, 1000);
};

// Aggressive logic, JS injection happens no matter the allowlist status
// Limited only to risky TLDs, beware adding new logic here as it could break sites.
if (
    Object.keys(tldRiskLevel).some(
        (tld) =>
            window.location.host && window.location.host.endsWith("." + tld)
    )
) {
    console.debug("TSS: Risky TLD, aggressive protection");
    mbtss.initScriptListener();
    if (!isMV3()) {
        injectJavascript(`
        let originalFunc = window.Notification.requestPermission;
        window.Notification.requestPermission = function () {
            console.debug("TSS: Detected a suspicious permission request");
            let parameters = [].slice.call(arguments);
            window.top.postMessage(
                JSON.parse(
                    JSON.stringify({
                        type: "scam",
                        subtype: "notificationLoop",
                        parameters,
                        nonce: "${mbtss.nonce}",
                    })
                ),
                "*"
            );
            return originalFunc.apply(this, parameters);
        };
    `);
    } else {
        injectJsFile('injection-tss-notification.js', {nonce: mbtss.nonce});
    }
}

// Skimmer protection bypasses the allowlist due to its prevalence on popular sites
// https://malwarebytes.atlassian.net/browse/BG-1337
const injectSkimmerProtection = () => {
    console.debug("TSS: Init skimmer protection");
    if (!isMV3()) {
        injectJavascript(`
            setTimeout(() => (devtools = true), 1000);
            window.Firebug = {chrome: {isInitialized: true}};
        `);
    } else {
        injectJsFile('injection-tss-skimmer.js');
    }
};

const checkIsTssExcluded = () => {
    return new Promise((resolve) => {
        chrome.runtime.sendMessage(
            { type: "isExcluded", parameters: { type: "scam", url: mbtssUrl, tabId: tabId } },
            (response) => {
                response && console.debug('TSS: excluded result: ', response.excluded);
                response && response.excluded ? resolve(true) : resolve(false);
            }
        );
    });
};

const isLocalFile = window.location.href.startsWith('file://')

if (!isLocalFile) {
    mbtss.getTabId().then((id) => {
        tabId = id;
    
        //BEFORE SETUP, CHECK FOR EXCLUSION, CHECK FOR TEMPORARY EXCLUSION TOO
        checkIsTssExcluded().then((excluded) => {
            if (excluded) {
                mbtss.onExclusion("send-mesage");
            } else {
                mbtss.setup();
            }
        });
    }).catch((err) => {
        console.debug("TSS: Error occurred while getting tab id. " + err);
    });
    
    if (checkoutRegex.test(window.location)) {
        chrome.runtime.sendMessage(
            { type: MSG_SETTINGS_GET, payload: SETTING_SKIMMER_PROTECTION },
            (resp) => {
                if (!resp || resp.error) {
                    return console.debug("TSS: skimmer error ", resp);
                }
                if (resp && resp.success) {
                    injectSkimmerProtection();
                }
            }
        );
    }
} else {
    console.log('TSS: Skipping TSS protection for local file');
}
