import { MSG_CHECK_PERMISSION } from '@/app/scripts/app-consts';
import { chrome } from '../utils/polyfill';
import { urlHost } from '../utils/utils.js';
import { displayShellInjectionNotification } from './content-notifications.js';

type AllowScamResponse = {
  success?: boolean;
  error?: string;
  excluded?: boolean;
};

type SendMessageResponse = {
  success?: boolean;
  error?: string;
};

export class ShellProtector {
  private static isActive = true;

  // Sends message to allow scams on this host
  static async allowScamsOnThisSite(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      chrome.runtime.sendMessage(
        {
          type: "MSG_ADD_ALLOW",
          payload: { host: urlHost(window.location.href) },
        },
        (response: AllowScamResponse) => {
          if (!response || response.error) {
            reject(response?.error ?? "Unknown error");
          } else {
            resolve(!!response.success);
          }
        }
      );
    });
  }

  static async sendMessageToTheServiceWorker(message: string, where: string = "debug"): Promise<boolean> {
    return new Promise((resolve, reject) => {
      chrome.runtime.sendMessage(
        {
          type: "MSG_APPEND_CONSOLE_LOG",
          payload: { host: urlHost(window.location.href), message, where },
        },
        (response: SendMessageResponse) => {
          if (!response || response.error) {
            reject(response?.error ?? "Unknown error");
          } else {
            resolve(!!response.success);
          }
        }
      );
    });
  }

  // Attaches click events to disable warning buttons
  static attachEvents(dom: Document): void {
    const buttonIds = [
      "mb-disable-shell-warning",
      "mb-disable-search-hijacking-warning",
    ];

    buttonIds.forEach((id) => {
      const button = dom.getElementById(id);
      if (button) {
        button.addEventListener("click", async () => {
          try {
            await ShellProtector.allowScamsOnThisSite();
            ShellProtector.isActive = false;
            const root = document.getElementById("malwarebytes-root");
            root?.remove();
          } catch (error) {
            console.error("CONTENT_SHELL: Failed to disable warning", error);
          }
        });
      }
    });
  }

  // Checks whether the current page is excluded
  static async isExcluded(): Promise<boolean> {
    return new Promise((resolve) => {
      chrome.runtime.sendMessage(
        {
          type: "isExcluded",
          parameters: { type: "scam", url: window.location.href },
        },
        (response: AllowScamResponse) => {
          resolve(!!response?.excluded);
        }
      );
    });
  }

  // Shell injection and command injection patterns
  private static readonly shellPatterns: RegExp[] = [
    /^.*;\s*(.|\s)*$/gm,                     // command1 ; command2
    /^.+\s*\|\s*.+$/gm,                      // curl http://... | sh
    /^\w+\s*`.+`$/gm,                        // command `subcommand`
    /^\w+\s*'.+'$/gm,                        // command 'string'
    /^.+\$\(\w+.*\)$/gm,                     // $(command)
    /\b(curl|ls|rm|cp|mv|touch|cd|wget|cmd|mshta)\b\s+/gm, // suspicious shell keywords
    /\bcmd\s+\/c\s+start\s+(\/min\s+)?powershell\b/gi,     // Windows cmd launching PowerShell
  ];

  // Checks if the given text matches any suspicious pattern
  static isSuspiciousText(text: string): boolean {
      // resetting lastIndex to 0 for global patterns https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/RegExp/lastIndex
      return this.shellPatterns.some((pattern) => {
        pattern.lastIndex = 0; 
        return pattern.test(text);
      });
  }

  // Injects a warning into the DOM
  static injectWarning(): void {
    displayShellInjectionNotification(this.attachEvents);
  }
  // Cache for clipboard permission status
  private static clipboardPermissionCache: boolean | null = null;

  private static async checkClipboardPermission(): Promise<boolean> {
    if (this.clipboardPermissionCache !== null) {
      return this.clipboardPermissionCache;
    }

    return new Promise<boolean>((resolve) => {
      chrome.runtime.sendMessage(
        { type: MSG_CHECK_PERMISSION, payload: { permissions: ['clipboardRead'] } },
        (response) => {
          if (chrome.runtime.lastError) {
            console.warn("checkClipboardPermission: Error checking permission", chrome.runtime.lastError);
            this.clipboardPermissionCache = false;
          } else {
            this.clipboardPermissionCache = !!response?.hasPermission;
          }
          resolve(this.clipboardPermissionCache);
        }
      );
    });
  }


static async getClipboardContent(event: ClipboardEvent): Promise<string> {
  // 1. Try to get it from the event directly
  const fromEvent = event.clipboardData?.getData("text/plain");
  if (fromEvent && fromEvent.trim()) {
    return fromEvent;
  }

  // 2. Try navigator.clipboard.readText()
  try {
    // Check if clipboard permission is available via message to background script
    const hasPermission = await this.checkClipboardPermission();
    
    if (!hasPermission) {
      console.debug("getClipboardContent: clipboardRead permission not granted in extension");
      return '';
    }

    const text = await navigator.clipboard.readText();
    if (text && text.trim()) {
      return text;
    }

    // 3. Try navigator.clipboard.read() for richer clipboard access
    try {
      const items = await navigator.clipboard.read();
      for (const item of items) {
        if (item.types.includes("text/plain")) {
          const blob = await item.getType("text/plain");
          const blobText = await blob.text();
          if (blobText && blobText.trim()) {
            return blobText;
          }
        }
      }
    } catch (err) {
      console.debug("getClipboardContent: navigator.clipboard.read() failed", err);
    }

  } catch (err) {
    console.debug("getClipboardContent: navigator.clipboard.readText() failed", err);
  }

  // 4. Fallback
  return "";
}

  static async setClipboardContent(text: string): Promise<boolean> {
    // 1. Try the modern Clipboard API
    try {
      await navigator.clipboard.writeText(text);
      console.debug("setClipboardContent: Successfully wrote using navigator.clipboard.writeText()");
      return true;
    } catch (err) {
      console.warn("setClipboardContent: navigator.clipboard.writeText() failed", err);
    }

    // 2. Fallback using a hidden <textarea> and execCommand('copy')
    try {
      const textarea = document.createElement("textarea");
      textarea.value = text;
      textarea.style.position = "fixed";
      textarea.style.top = "-9999px";
      textarea.style.left = "-9999px";
      textarea.style.opacity = "0";
      textarea.setAttribute("readonly", "");

      document.body.appendChild(textarea);
      textarea.select();
      const successful = document.execCommand("copy");
      document.body.removeChild(textarea);

      if (successful) {
        console.debug("setClipboardContent: Successfully wrote using execCommand fallback");
        return true;
      } else {
        console.warn("setClipboardContent: execCommand fallback failed");
      }
    } catch (err) {
      console.error("setClipboardContent: execCommand fallback error", err);
    }

    return false;
  }

  // Initializes shell protection
  static async init(): Promise<void> {
    const excluded = await this.isExcluded();
    if (excluded) {
      console.debug("CONTENT_SHELL: Page is excluded. Skipping shell protection.");
      return;
    }

    document.addEventListener("copy", async (event: ClipboardEvent) => {
      try {
        const clipboardText = await ShellProtector.getClipboardContent(event);
        const target = event.target as HTMLElement | null;
        const targetText = target?.innerText ?? "";

        let shouldWarn = ShellProtector.isSuspiciousText(clipboardText) || ShellProtector.isSuspiciousText(targetText);

        if (shouldWarn && ShellProtector.isActive) {
          const sanitizedClipboardText = clipboardText
            .replace(/[\n\r]/g, "") // Replace newlines
            .replace(/[^\x20-\x7E]/g, ""); // Replace non-printable characters with 
          const msg = `CONTENT_SHELL: Suspicious copy detected: ${sanitizedClipboardText}`;
          await this.sendMessageToTheServiceWorker(msg, "log");
          ShellProtector.setClipboardContent(`[ Suspicious Content] ${clipboardText}`);
          ShellProtector.injectWarning();
        }
      } catch (error) {
        console.error("CONTENT_SHELL: Error handling copy event", error);
      }
    });

  }
}

// Start the script
ShellProtector.init();
