/*
* 
*  Refrence: https://developer.mozilla.org/en-US/docs/Web/API/HTMLAnchorElement/ping
* 
*/

import {
    MSG_TAB_ID_GET,
} from '@/app/scripts/app-consts';
import {
    isAdsProtectionActive,
    recordPingTrackerRemoval,
    isPingTrackerExcluded,
} from '@/content-scripts/common-utils';
import { settingsGetAsync } from '@/app/scripts/ui-utils/ui-utils.js';
import { FLAG_ENABLE_PING_TRACKER_REMOVER } from '@/app/scripts/app-consts.js';



export function getCurrentTabId(): Promise<{ tabId: number }> {
    return new Promise((resolve, reject) => {
        chrome.runtime.sendMessage({ type: MSG_TAB_ID_GET }, (resp) => {
            if (!resp) {
                reject(new Error("Error getting tab info"));
            } else {
                resolve(resp);
            }
        });
    });
}

export function removePingAttributes(tabId: number, pageUrl: string): NodeListOf<Element> {
    const pingLinks = document.querySelectorAll('a[ping]');
    pingLinks.forEach(link => {
        link.removeAttribute('ping');
        recordPingTrackerRemoval(tabId, pageUrl);
    });
    return pingLinks;
};

export function handlePingSanitizer(tabId: number, pageUrl: string): void {


    // Run the function immediately after DOMContentLoaded
    const pingLinks = removePingAttributes(tabId, pageUrl);

    // Optionally, use a MutationObserver to catch dynamically added elements
    const observer = new MutationObserver(mutations => {
        mutations.forEach(mutation => {
            if (mutation.type === 'childList') {
                mutation.addedNodes.forEach(node => {
                    if (node.nodeType === Node.ELEMENT_NODE) {
                        if (node instanceof HTMLAnchorElement && node.hasAttribute('ping')) {
                            node.removeAttribute('ping');
                            recordPingTrackerRemoval(tabId, pageUrl);
                        } else {
                            // Check for <a> elements within added subtrees.
                            const addedPingLinks = (node as Element).querySelectorAll('a[ping]');
                            addedPingLinks.forEach(link => {
                                link.removeAttribute('ping');
                                recordPingTrackerRemoval(tabId, pageUrl);
                            });
                        }
                    }
                });
            } else if (mutation.type === 'attributes' && mutation.attributeName === 'ping' && mutation.target instanceof HTMLAnchorElement) {
                mutation.target.removeAttribute('ping');
                recordPingTrackerRemoval(tabId, pageUrl);
            }
        });
    });

    observer.observe(document.body, {
        childList: true,
        subtree: true,
        attributes: true,
        attributeFilter: ['ping']
    });
}


document.addEventListener('DOMContentLoaded', async () => {
    try {
        const pageUrl = window.location.hostname;
        const { tabId } = await getCurrentTabId();

        const isAdsProtectionActv = await isAdsProtectionActive(tabId, pageUrl) // is ads protection active
        const isPingTrackerExcld = await isPingTrackerExcluded(pageUrl) // is ping tracker excluded for this domain
        const isPingTrackerFlagOn = await settingsGetAsync(FLAG_ENABLE_PING_TRACKER_REMOVER); // is ping tracker remover active

        if (isAdsProtectionActv && !isPingTrackerExcld && isPingTrackerFlagOn) {
            handlePingSanitizer(tabId, pageUrl);
        }

    } catch (err) {
        console.error('PING_SAN: Error getting tab info, :', err);
    }
});