import {chrome} from "../../../utils/polyfill.ts";
import {MSG_RECORD_CONTENT_AD} from "../app-consts";

const adTexts = {
    en: {
        isSponsored: (str) => str.endsWith("ponsored"), //sponsored
        isSuggested: (str) => str.endsWith("uggested for you"), //suggested for you
    },
    es: {
        isSponsored: (str) => str.endsWith("ublicidad"), //publicidad
        isSuggested: (str) => str.endsWith("ugerencia para ti"), //sugerencia para ti
    },
    pt: {
        isSponsored: (str) => str.endsWith("atrocinado"), //patrocinado
        isSuggested: (str) =>
            str.endsWith("ugestes para voc") || str.endsWith("ugestes para ti"), //sugestes para voc, sugestes para ti
    },
    de: {
        isSponsored: (str) => str.endsWith("esponsert"), //gesponsert
        isSuggested: (str) => str.endsWith("orschlge fr dich"), //vorschlge fr dich
    },
    fr: {
        isSponsored: (str) =>
            str.endsWith("ponsoris") || str.endsWith("ommandit"), //sponsoris, commandit
        isSuggested: (str) => str.endsWith("uggestion pour vous"), //suggestion pour vous
    },
    it: {
        isSponsored: (str) => str.endsWith("ponsorizzato"), //sponsorizzato
        isSuggested: (str) => str.endsWith("ontenuto suggerito per te"), //contenuto suggerito per te
    },
    nl: {
        isSponsored: (str) => str.endsWith("esponsord"), //gesponsord
        isSuggested: (str) => str.endsWith("oorgesteld voor jou"), //voorgesteld voor jou
    },
    pl: {
        isSponsored: (str) => str.endsWith("ponsorowane"), //sponsorowane
        isSuggested: (str) => str.endsWith("roponowana dla ciebie"), //proponowana dla ciebie
    },
    ru: {
        isSponsored: (str) => str.endsWith(""), //
        isSuggested: (str) => str.endsWith("  "), //  s
    },
};

const recordBlocked = (tab) => {
    try {
    // record the ad and then update the badge count
        chrome.runtime.sendMessage({
            type: MSG_RECORD_CONTENT_AD,
            tabId: tab.id,
            tabUrl: tab.url,
        });
    } catch (err) {
        console.error("RBAD: Error occurred while recording blocked ad. " + err);
    }
};

export const blockSponsoredAndSuggested = (lang, tabInfo) => {
    const config = {childList: true, subtree: true};

    let observer = new MutationObserver((mutationList) => {
        for (let anElement of mutationList) {
            if (0 === anElement.addedNodes.length) {
                continue;
            }
            anElement.addedNodes.forEach((aNode) => {
                if ("div" !== aNode.localName) {
                    return;
                }

                removeSideSponsorship(tabInfo, lang);
                removeElements(aNode, lang, tabInfo);
                removeVisualArtifacts();
            });
        }
    });
    observer.observe(document.body, config);
    removeSideSponsorship(tabInfo, lang);
    let initialElements = document.querySelectorAll("div[aria-labelledby]");
    initialElements.forEach((aNode) => {
        removeElements(aNode, lang, tabInfo);
    });
};


function removeVisualArtifacts() {
    document.querySelectorAll("div[class='__fb-light-mode'] > svg").forEach((el) => {
        el.style.width = "1px !important";
        el.style.height = "1px !important";
        el.style.display = "none";
    });

    document.querySelectorAll("div[class='__fb-dark-mode'] > svg").forEach((el) => {
        el.style.width = "1px !important";
        el.style.height = "1px !important";
        el.style.display = "none";
    });
}

function removeElements(aNode, lang, tab) {
    let frameArr = Array.from(
        aNode.querySelectorAll("div[aria-labelledby]")
    );
    removeSponsored(frameArr, lang, tab);
    removeSuggested(frameArr, lang, tab);
}

function removeSponsored(frameArr, lang, tab) {
    frameArr.forEach((frame) => {
        let useElement = frame.querySelector("div > span > span > span > span > a > span > span use");
        if (!useElement) {
            return;
        }
        let id = useElement.getAttribute("xlink:href");
        if (!id) {
            return;
        }
        const txt = document.getElementById(id.slice(1));
        const svg = txt.parentElement;
        if (txt && adTexts[lang] && adTexts[lang].isSponsored(txt.textContent)) {
            window.useLogging && console.log("Found sponsored: " + txt.textContent);
            try {
                frame.style.display = "none";
                svg.parentNode.style.display = "none";
                recordBlocked(tab);
            } catch (error) {
                window.useLogging && console.error(error);
            }
        }
    });
}

function removeSuggested(frameArr, lang, tab) {
    frameArr.forEach((frame) => {
        let suggestion = frame.querySelector("div > div > div > span");
        if (suggestion && suggestion.innerText && adTexts[lang] && adTexts[lang].isSuggested(suggestion.innerText)) {
            window.useLogging && console.log("Found suggested: " + suggestion.innerText);
            try {
                frame.style.display = "none";
                recordBlocked(tab);
            } catch (error) {
                window.useLogging && console.error(error);
            }
        }
    });
}

function removeSideSponsorship(tabInfo, lang) {
    try {
        let leftFrameItems = document.
            querySelectorAll("div[role='complementary'] * span > div > div > div");
        leftFrameItems.forEach((item) => {
            if (adTexts[lang].isSponsored(item.innerText) &&
            item.parentElement.style.display !== "none") {
                item.parentElement.style.display = "none";
                recordBlocked(tabInfo);
            }
        });
    } catch (error) {
        window.useLogging && console.error(error);
    }
}
