import { MSG_CONTEXT_MENU_TARGET_URLS } from "../app/scripts/app-consts";
import { chrome } from "../utils/polyfill.ts";
import { getXPath } from "../utils/utils";

// The chrome extension context menu API does not always provide enough information to determine the target URL 
// so we need to extract the urls from the clicked element and its children and send them to the background script for pre caching
document.addEventListener('contextmenu', function (event) {
    handleContextMenuEvent(event);
});

function handleContextMenuEvent(event) {
    // check the clicked element and its children for:
    // - a:hrefs
    // - img:src
    // - picture:source:srcset
    window.useLogging && console.log('CMN: Context menu clicked. Event -> ', { event });
    const urls = recursivelyExtractUrls(event.target);
    if (urls.length > 10) {
        // user probably right-clicked on a large image or a page with many links, or not on an image
        // we don't care about this case
        return;
    }

    // get the xpath of the clicked element for telemetry
    const xpath = getXPath(event.target);
    window.useLogging && console.debug('CMN: XPath of clicked element: ', { xpath });

    // send the urls to the background script
    chrome.runtime.sendMessage({ type: MSG_CONTEXT_MENU_TARGET_URLS, urls: urls, xpath: xpath });
}


// recursively extract urls from a target node and its children if it has any
function recursivelyExtractUrls(target) {
    let urls = [];

    if (target.tagName === 'A') {
        urls.push(target.href);
    } else if (target.tagName === 'IMG') {
        urls.push(target.src);
    } else if (target.tagName === 'PICTURE') {
        const sources = target.querySelectorAll('source');
        for (const source of sources) {
            urls.push(source.srcset);
        }
    }

    if (target.children.length > 0) {
        for (const child of target.children) {
            urls = [...urls, ...recursivelyExtractUrls(child)];
        }
    }

    return urls;
}
