export const clickTheSkip = () => {
    let btnSkip = document.querySelector(".ytp-ad-skip-button,.ytp-ad-skip-button-modern,.ytp-skip-ad,#skip-ad");
    if (btnSkip) {
        btnSkip.click();
        window.useLogging && console.log("Clicked skip button");
    }
    let btnCross = document.getElementsByClassName("ytp-ad-overlay-close-container");
    if (btnCross.length > 0) {
        btnCross[0].click();
        window.useLogging && console.log("Clicked close button");
    }
};

export function skipToTheEnd() {
    const adVideo = document.querySelectorAll(".ad-showing")[0] ||
        document.querySelector(".ytp-ad-text.ytp-ad-preview-text") ||
        document.querySelector("ytp-ad-preview-container");
    const video = document.querySelector("video");
    if (adVideo != undefined && video != undefined) {
        window.useLogging && console.log("Skipping to the end of the video", adVideo);
        video.muted = true;
        video.src = "";
        if (video.paused) {
            video.play();
        }
    }
}

export function removeKnownAds() {
    const tags = [
        {"tag": "ytd-rich-item-renderer ytd-ad-slot-renderer", "remove": true, "refresh": false, "parentLevel": 2},
        {"tag": ".ytd-ad-slot-renderer", "remove": true, "refresh": false},
        {"tag": "tp-yt-iron-overlay-backdrop", "remove": true, "refresh": false,
            "addClasses": ["opened"],
            "removeClasses": ["closed"],
        },
        {"tag": "#movie_player", "remove": false, "refresh": false, 
            "addClasses": ["ad-created", "playing-mode"], 
            "removeClasses": [
                "ad-interrupting", 
                "ytp-hide-controls", 
                "ytp-ad-display-override", 
                "ended-mode", 
                "unstarted-mode", 
                "paused-mode", 
            ]},
    ];

    for (const ad of tags) {
        let elements = document.querySelectorAll(ad.tag);
        if (elements.length === 0) {
            continue;
        }
        if (ad.remove) {
            window.useLogging && console.log("Removing: ", ad.tag);
            elements.forEach((e) =>  {
                if (ad.parentLevel) {
                    let backCount = ad.parentLevel || 1;
                    while (backCount > 0) {
                        e = e.parentNode;
                        backCount--;
                    }
                }
                e.remove();
            });
        }
        if (ad.removeClasses) {
            elements.forEach((e) => e.classList.remove(...ad.removeClasses));
        }
        if (ad.addClasses) {
            elements.forEach((e) => e.classList.add(...ad.addClasses));
        }
        if (ad.refresh) {
            window.useLogging && console.log("Refreshing: ", ad.tag);
            window.location.reload();
        }
    }
}
